"use client";

import { useRef, useEffect } from "react";
import { FiArrowRight } from "react-icons/fi";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

import { Button } from "@/components/ui/Button";
import { MagneticButton } from "@/components/animations/MagneticButton";
import { ParticlesBackground } from "@/components/animations/ParticlesBackground";

if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger);
}

export const Budget = () => {
  const sectionRef = useRef<HTMLElement>(null);
  const contentRef = useRef<HTMLDivElement>(null);
  const bgRef = useRef<HTMLDivElement>(null);
  const lineRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (typeof window === "undefined") return;

    const ctx = gsap.context(() => {
      // Parallax background
      if (bgRef.current) {
        gsap.to(bgRef.current, {
          yPercent: -20,
          ease: "none",
          scrollTrigger: {
            trigger: sectionRef.current,
            start: "top bottom",
            end: "bottom top",
            scrub: true,
          },
        });
      }

      // Content animation
      if (contentRef.current) {
        gsap.fromTo(
          contentRef.current.children,
          { opacity: 0, y: 50, scale: 0.95 },
          {
            opacity: 1,
            y: 0,
            scale: 1,
            duration: 1,
            stagger: 0.2,
            ease: "power3.out",
            scrollTrigger: {
              trigger: contentRef.current,
              start: "top 80%",
              toggleActions: "play none none reverse",
            },
          }
        );
      }

      // Animated line at bottom
      if (lineRef.current) {
        gsap.fromTo(
          lineRef.current,
          { scaleX: 0 },
          {
            scaleX: 1,
            duration: 1.5,
            ease: "power3.out",
            scrollTrigger: {
              trigger: sectionRef.current,
              start: "top 60%",
              toggleActions: "play none none reverse",
            },
          }
        );
      }
    }, sectionRef);

    return () => ctx.revert();
  }, []);

  return (
    <section
      ref={sectionRef}
      id="get-started"
      className="relative py-[120px] overflow-hidden"
    >
      {/* Parallax background */}
      <div
        ref={bgRef}
        className="absolute inset-0 bg-budget bg-no-repeat bg-cover bg-center scale-110"
      />

      {/* Dark overlay base */}
      <div className="absolute inset-0 bg-gray-900/70" />

      {/* Interactive particles */}
      <ParticlesBackground
        className="z-[1] opacity-60"
        particleCount={40}
        particleSize={1.5}
        mouseRadius={150}
        connectDistance={100}
        color="100, 103, 242"
      />

      {/* Gradient overlays */}
      <div className="absolute inset-0 bg-gradient-to-r from-gray-900/60 via-transparent to-gray-900/60 z-[2]" />
      <div className="absolute inset-0 bg-gradient-to-t from-gray-900/80 via-transparent to-gray-900/80 z-[2]" />

      {/* Central glow */}
      <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[600px] h-[400px] bg-blue-500/15 rounded-full blur-3xl pointer-events-none z-[1]" />

      <div
        ref={contentRef}
        className="flex flex-col items-center justify-center mx-auto max-[1359px]:px-4 max-w-content relative z-10"
      >
        <span className="text-4xl md:text-5xl font-bold text-white text-center mb-6 leading-tight">
          Pronto para transformar <br className="hidden md:block" />
          seu negócio?
        </span>

        <p className="text-lg text-white/90 text-center mb-10 max-w-2xl mx-auto leading-relaxed">
          Fale com nossa equipe e receba um orçamento rápido e personalizado.
        </p>

        <MagneticButton strength={0.25}>
          <Button
            href="#contact"
            variant="white"
            className="group gap-2 max-w-[223px] hover:shadow-xl hover:shadow-white/20 transition-shadow duration-300"
          >
            Solicitar orçamento
            <FiArrowRight className="w-5 h-5 group-hover:translate-x-1 transition-transform" />
          </Button>
        </MagneticButton>
      </div>

      {/* Animated gradient line at bottom */}
      <div className="absolute bottom-0 left-0 right-0 h-[3px] z-20 overflow-hidden">
        <div
          ref={lineRef}
          className="h-full w-full origin-center"
          style={{
            background: "linear-gradient(90deg, transparent 0%, rgba(100, 103, 242, 0.3) 20%, rgba(100, 103, 242, 0.8) 50%, rgba(100, 103, 242, 0.3) 80%, transparent 100%)",
          }}
        />
        {/* Shimmer effect */}
        <div
          className="absolute inset-0 animate-shimmer"
          style={{
            background: "linear-gradient(90deg, transparent 0%, rgba(255, 255, 255, 0.3) 50%, transparent 100%)",
            backgroundSize: "200% 100%",
          }}
        />
      </div>
    </section>
  );
};
